import React, { useEffect} from "react";
import Axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import "./home.css";
import SearchBar from "material-ui-search-bar";
const Home = () => {
  const [value, setValue] = React.useState();
  const [searchItem , setSearchItem] = React.useState([]);
  
  function handleSearch(val) {
    Axios.post(`/compare/liveData`,{searchValue:val}).then((res) => {
      setSearchItem(res.data)
      //console.log(res.data);
    
    });
    
    console.log(val);
  }
  console.log(searchItem);
  return (
    <>
    <div>
    <SearchBar
    value={value}
      onChange={(newValue) => setValue(newValue)}
      onRequestSearch={(val) => handleSearch(val)}
      style={{
        margin: '20px auto',
        maxWidth: 800
      }}
    />
         </div>
     { searchItem.length > 0 ? 
      <div className="grid-container">
        <div className="item1" style={{fontSize:"35px"}}>{searchItem[0].headline}</div>
        <div className="item2">
        <iframe src={searchItem[0].imageUrl} width="800" height="200" allow="autoplay"></iframe>
        </div>
        <div className="item3" ><span>{searchItem[0].primaryText}</span><a href={searchItem[0].url} style={{marginLeft:"10px"}}>{searchItem[0].CTA}</a></div>
      </div> : ""
       }
    </>
  );
};

export default Home;
